smart-video-embedded {
  display: block;
}
.smart-video-embedded .sev-inner {
  display: block;
  width: 100%;
  height: 100%;
}
.smart-video-embedded iframe {
  border: 0;
}
.smart-video-embedded .video-js {
  height: 100%;
  width: 100%;
}
.img-container > smart-video-embedded {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.video-embedded-buttons {
  text-align: center;
}
.video-embedded-buttons .btn {
  text-transform: uppercase;
  padding: 10px 30px;
}
smart-image {
  display: block;
  width: 100%;
  height: 0;
  padding: 0;
  text-align: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
smart-image[mode='origin'] {
  display: inline-block;
  width: auto;
  height: auto;
}
smart-image[mode='cover'] {
  height: 100%;
}
smart-image[mode='save-ratio'] {
  height: 0;
}
smart-image[mode='save-ratio']:not([loaded]) {
  min-height: 1px;
}
smart-image:after {
  display: none;
  content: attr(alt);
}
smart-image[error]:after {
  display: inline-block;
}
smart-image[loaded]:after {
  display: none;
}
smart-image.bg-flex {
  background-size: 100% 100%;
}
smart-image.bg-inscribe {
  background-size: contain;
}
smart-image.bg-v-top {
  background-position-y: top;
}
smart-image.bg-v-center {
  background-position-y: center;
}
smart-image.bg-v-bottom {
  background-position-y: bottom;
}
smart-image.bg-h-left {
  background-position-x: left;
}
smart-image.bg-h-center {
  background-position-x: center;
}
smart-image.bg-h-right {
  background-position-x: right;
}
.img-container {
  position: relative;
  overflow: hidden;
}
.img-container smart-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: block;
}
.img-container smart-image[mode="origin"] {
  position: relative;
}
.img-container smart-image[mode='save-ratio'] {
  position: relative;
}
.img-container smart-image[lazy] {
  opacity: 0;
}
.img-container smart-image[lazy][ready] {
  opacity: 1;
  -webkit-transition: opacity 0.4s;
  transition: opacity 0.4s;
}
.img-container.img-container-no-rat smart-image {
  position: relative;
}
.img-container.img-container-1-1 {
  padding-top: 100%;
}
.img-container.img-container-1-1 smart-image {
  position: absolute;
}
.img-container.img-container-4-3 {
  padding-top: 75%;
}
.img-container.img-container-4-3 smart-image {
  position: absolute;
}
.img-container.img-container-16-9 {
  padding-top: 56.25%;
}
.img-container.img-container-16-9 smart-image {
  position: absolute;
}
